import { HashMap } from '../../../shared/utilities/types.utilities';

import * as faker from 'faker';
import { padZero, unique } from '../../../shared/utilities/general.utilities';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { User } from './user.class';
import { Subscription } from 'rxjs';

let USER_COUNT = 0;

export let USER_DOMAIN = 'acaprojects.com';

const USER_EMAILS: string[] = [];

/**
 * Generate raw mock data for a user
 * @param id Forced ID for the mock
 * @param name Forced name for the user
 * @param external Whether user is external of the organisation
 */
export function generateMockUser(override: HashMap = {}): HashMap {
    const id = `user-${USER_COUNT++}`;
    const name = `${faker.name.firstName()} ${faker.name.lastName()}`;
    const external = override.external || !((Math.random() * 99999) % 2);
    const organisation = external ? faker.company.companyName() : USER_DOMAIN.split('.')[0];
    let delegates: string[] = [];
    const delegate_count = Math.min(Math.random() * 4 + 1, USER_EMAILS.length);
    for (let i = 0; i < delegate_count; i++) {
        delegates.push(USER_EMAILS[Math.floor(Math.random() * USER_EMAILS.length)]);
    }
    delegates = unique(delegates);
    const email = `${name
        .split(' ')
        .join('.')
        .toLowerCase()}@${external ? 'not-' : ''}${USER_DOMAIN}`;
    USER_EMAILS.push(email);
    return {
        id,
        name,
        first_name: name.split(' ')[0],
        last_name: name.split(' ')[1],
        email,
        phone: faker.phone.phoneNumber(),
        visitor: external,
        organisation: {
            id: organisation
                .split(' ')
                .join('.')
                .toLowerCase(),
            name: organisation
        },
        department: faker.commerce.department(),
        staff_code: padZero(Math.floor(Math.random() * 99999), 5),
        delegates,
        image: faker.image.avatar(),
        ...override
    };
}

/**
 * Generate form fields for the given user
 * @param user User to generate form for
 */
export function generateUserForm(user: User): FormGroup {
    if (!user) {
        throw Error('No user passed');
    }
    const fields: HashMap<FormControl> = {
        id: new FormControl(user.id || ''),
        name: new FormControl(user.name || ''),
        first_name: new FormControl(user.first_name || ''),
        last_name: new FormControl(user.last_name || ''),
        email: new FormControl(user.email || '', [Validators.email, Validators.required])
    };
    // Generate form group for booking item
    const form = new FormGroup(fields);
    // Listen for changes on the fields and update booking
    if (form) {
        const subs: HashMap<Subscription> = {};
        Object.keys(form.controls).forEach((key: any) => {
            subs[`change_${key}`] = form.controls[key].valueChanges.subscribe(value =>
                user.storePendingChange(key, value)
            );
        });
        subs.user = user.changeEvents.subscribe(event => {
            if (event.type === 'item_saved') {
                for (const key in subs) {
                    if (subs[key] instanceof Subscription) {
                        subs[key].unsubscribe();
                    }
                }
            }
        });
    }
    return form;
}
