import { BaseDataClass } from '../base-api.class';
import { BaseAPIService } from '../base.service';

import { BookingNote, BookingMutableFields, BookingRecurrenceDetails } from './booking.interfaces';
import { CateringOrder } from '../catering/catering-order.class';
import { User } from '../users/user.class';
import { HashMap } from '../../../shared/utilities/types.utilities';
import { humaniseDuration, unique, flatten } from '../../../shared/utilities/general.utilities';
import { Space } from '../spaces/space.class';

import * as dayjs from 'dayjs';

export class Booking extends BaseDataClass {
    /** Unique calendar event ID */
    public readonly icaluid: string;
    /** Subject or title of the booking */
    public readonly title: string;
    /** Unix timestamp of the booking start */
    public readonly date: number;
    /** Length of the booking in minutes */
    public readonly duration: number;
    /** Description or details of the booking */
    public readonly body: string;
    /** Type of booking */
    public readonly type: string;
    /** Whether the booking has catering associated with it */
    public readonly has_catering: boolean;
    /** List of catering orders for the booking */
    public readonly catering: readonly CateringOrder[];
    /** String location set in booking */
    public readonly _location: string;
    /** Whether booking's duration covers all day */
    public readonly all_day: boolean;
    /** Details of the recurrence */
    public readonly recurrence: BookingRecurrenceDetails;
    /** Mapping of emails to approval statuses */
    public readonly approval_status: HashMap<string>;
    /** List of spaces associated with the booking */
    public readonly space_ids: readonly string[];
    /** List of attendee emails associated with the booking */
    public readonly attendee_emails: readonly string[];
    /** Email of the host or organiser of the booking */
    public readonly organiser_email: string;
    /** Email of the host or organiser of the booking */
    public readonly creator_email: string;
    /** List of notes associated with the booking */
    public readonly notes: readonly BookingNote[];
    /** Mapping of spaces to equipment charge codes */
    public readonly equipment_codes: HashMap<string>;
    /** Mapping of spaces to expected number of attendees */
    public readonly expected_attendees: HashMap<number>;
    /** Setup time before the meeting in minutes */
    public readonly setup: number;
    /** List of checked in attendees */
    public readonly checked_in: string[];
    /** Breakdown time after booking in minutes */
    public readonly breakdown: number;
    /** List of fields edited since creation */
    public readonly edits: string[];
    /** List of users to be notified on visitor arrivals */
    public readonly notify_users: readonly string[];
    /** Map of display strings */
    private _display: HashMap<string> = {};
    /** Class type of required service */
    protected __type: string = 'Booking';

    constructor(raw_data: HashMap) {
        super(raw_data);
        const u_service = this._service.parent.Users;
        this.icaluid = raw_data.icaluid || this.id || '';
        this.title = raw_data.title || raw_data.subject || raw_data.Subject || '';
        let start = dayjs(
            raw_data.date || raw_data.start_epoch * 1000 || raw_data.start || raw_data.Start
        ).startOf('m');
        if (!this.id) {
            start = start.minute(Math.ceil(start.minute() / 5) * 5);
        }
        this.date = start.valueOf();
        this.duration =
            Math.max(
                0,
                raw_data.duration ||
                    dayjs(raw_data.end_epoch * 1000 || raw_data.end || raw_data.End).diff(
                        start,
                        'm'
                    )
            ) || 60;
        this.body =
            (raw_data.body instanceof Object ? raw_data.body.content : raw_data.body) ||
            raw_data.description ||
            '';
        this.type =
            raw_data.booking_type ||
            raw_data.type ||
            (raw_data.visitors ? 'external' : null) ||
            'internal';
        this.has_catering = !!raw_data.has_catering;
        this.attendee_emails = (raw_data.attendees || raw_data._attendees || []).map((i) =>
            i instanceof Object ? i.email || i.id : i
        );
        const organiser = raw_data.organiser || raw_data.organizer || u_service.current || {};
        this.organiser_email =
            raw_data.organiser_email ||
            organiser.email ||
            raw_data._organiser ||
            raw_data.organiser;
        const creator = raw_data.creator || raw_data.booked_by || organiser;
        this.creator_email = raw_data.creator_email || creator.email || '';
        this._location = raw_data.location_name || raw_data.location || '';
        this.all_day = raw_data.all_day || this.duration > 23 * 60;
        this.setup = raw_data.setup || 0;
        this.breakdown = raw_data.breakdown || 0;
        this.recurrence = raw_data.recurrence
            ? {
                  ...raw_data.recurrence,
                  start: raw_data.recurrence.start,
                  end: raw_data.recurrence.end,
              }
            : {};
        if (this.recurrence.end && this.recurrence.end < new Date().getTime()) {
            (this as any).recurrence = {
                ...this.recurrence,
                start: this.recurrence.start * 1000,
                end: this.recurrence.end * 1000,
            };
        }
        this.notes = raw_data.notes || [];
        this.equipment_codes = raw_data.equipment_codes || raw_data.equipment_code || {};
        this.expected_attendees = raw_data.expected_attendees || {};
        this.approval_status = raw_data.approval_status || raw_data.status || {};
        this.checked_in = raw_data.checked_in || raw_data.checkins || [];
        this.notify_users = raw_data.notify_users || [];
        if (raw_data.description && !this.notes.find((i) => i.type === 'description')) {
            this.notes = [
                ...this.notes,
                {
                    type: 'description',
                    date: -1,
                    message: raw_data.description,
                    author: this.organiser_email,
                },
            ];
        }
        this._display = {
            date: start.format('DD MMM YYYY'),
            time: `${start.format('h:mma')} - ${dayjs(start)
                .add(this.duration, 'm')
                .format('h:mma')}`,
            start: start.format('h:mma'),
            end: dayjs(start).add(this.duration, 'm').format('h:mma'),
            duration: humaniseDuration(this.duration),
        };
        this.space_ids = unique(
            raw_data.room_ids || raw_data.room_id || raw_data._space_list || []
        );
        if (raw_data.show_as && raw_data.show_as === 'cancelled') {
            this.space_ids.forEach((id) => (this.approval_status[id] = 'declined'));
        }
        if (raw_data.show_as && raw_data.show_as === 'tentative') {
            this.space_ids.forEach((id) => (this.approval_status[id] = 'tentative'));
        }
        // Add attendees and organiser to service data
        if (this._service.parent && this._service.parent.Users) {
            if (raw_data.attendees) {
                raw_data.attendees.forEach((u) => {
                    if (!this._service.parent.Users.find(u.email)) {
                        this._service.parent.Users.addFrom(this.id, u, 'class');
                    }
                });
            }
            if (organiser) {
                if (!this._service.parent.Users.find(organiser.email)) {
                    this._service.parent.Users.addFrom(this.id, organiser, 'class');
                }
            }
        }
        this.catering = (raw_data.catering instanceof Array ? raw_data.catering : []).map(
            (i) => new CateringOrder(i)
        );
        this.edits =
            raw_data.edits instanceof Array
                ? raw_data.edits
                : unique(flatten(Object.keys(raw_data.edits || {}).map((i) => raw_data.edits[i])));
    }

    /** Whether booking has been declined */
    public get declined(): boolean {
        for (const email of this.space_ids) {
            if ((this.approval_status[email] || '').indexOf('decline') >= 0) {
                return true;
            }
        }
        return false;
    }

    /** Whether booking has been approved */
    public get approved(): boolean {
        for (const email of this.space_ids) {
            if (
                this.approval_status[email] &&
                (this.approval_status[email].indexOf('tentative') >= 0 ||
                    this.approval_status[email].indexOf('decline') >= 0)
            ) {
                return false;
            }
        }
        return true;
    }

    /** Whether booking is tentative */
    public get tentative(): boolean {
        for (const email of this.space_ids) {
            if (
                this.approval_status[email] &&
                this.approval_status[email].indexOf('tentative') >= 0
            ) {
                return true;
            }
        }
        return false;
    }

    /** Whether booking contains external visitors in the attendee list */
    public get has_visitors(): boolean {
        return this.attendees.reduce((a, v) => a || v.external, false);
    }

    /** List of attendees to the meeting */
    public get attendees(): User[] {
        return this._service && this._service.parent && this._service.parent.Users
            ? this.attendee_emails.map(
                  (user) =>
                      this._service.parent.Users.find(user) ||
                      new User({ id: user, name: user, email: user })
              )
            : [];
    }

    /** Host or organiser of the meeting */
    public get organiser(): User {
        return this._service &&
            this._service.parent &&
            this._service.parent.Users &&
            this.organiser_email
            ? this._service.parent.Users.find(this.organiser_email)
            : null;
    }

    /** Creator of the meeting */
    public get creator(): User {
        return this._service &&
            this._service.parent &&
            this._service.parent.Users &&
            this.creator_email
            ? this._service.parent.Users.find(this.creator_email)
            : null;
    }

    /** Status of the booking */
    public get status(): 'future' | 'upcoming' | 'done' | 'started' | 'in_progress' {
        const now = dayjs();
        const date = dayjs(this.date);
        if (now.isBefore(date.subtract(15, 'm'), 'm')) {
            return 'future';
        } else if (now.isBefore(date, 'm')) {
            return 'upcoming';
        } else if (now.isBefore(date.add(15, 'm'), 'm')) {
            return 'started';
        } else if (now.isBefore(date.add(this.duration, 'm'), 'm')) {
            return 'in_progress';
        }
        return 'done';
    }

    /** Description of the booking purpose */
    public get description(): string {
        const note = (this.notes || []).find((i) => i.type === 'description');
        return note ? note.message : '';
    }

    /** Get the first space from the space list */
    public get space(): Space {
        return this._service &&
            this._service.parent &&
            this._service.parent.Spaces &&
            this.space_ids &&
            this.space_ids.length > 0
            ? this._service.parent.Spaces.find(this.space_ids[0])
            : null;
    }

    /** Spaces associated with the booking */
    public get space_list(): Space[] {
        return this._service &&
            this._service.parent &&
            this._service.parent.Spaces &&
            this.space_ids
            ? this.space_ids.map(
                  (i) =>
                      this._service.parent.Spaces.find(i) || new Space({ id: i, name: i, email: i })
              )
            : [];
    }

    /** Display value for the date */
    public get date_string(): string {
        return this._display.date;
    }
    /** Display valuie for the start and end times of the booking */
    public get time_period(): string {
        return this._display.time;
    }
    /** Display value for the start time of the booking */
    public get start_time(): string {
        return this._display.start;
    }
    /** Display value for the end time of the booking */
    public get end_time(): string {
        return this._display.end;
    }
    /** Display value for the duration of the booking */
    public get length_string(): string {
        return this._display.duration;
    }
    /** Display value for the location of the booking */
    public get location(): string {
        return this._display.space_names || this._location || 'No location set';
    }
    /** Display value for the level of the first space in the booking */
    public get level(): string {
        return this._display.level;
    }

    /**
     * Store new value for given field
     * @param key Name of the property
     * @param value New value associated with the property
     */
    public storePendingChange(key: BookingMutableFields, value: Booking[BookingMutableFields]) {
        return super.storePendingChange(key as any, value as any);
    }

    /**
     * Make a copy of this object
     */
    public clone(): Booking {
        return new Booking(this);
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(): Booking {
        return new Booking({
            ...this.toJSON(),
            id: null,
            email: null,
            icaluid: null,
        });
    }

    /**
     * Convert object into plain object
     */
    public toJSON(this: Booking, with_changes: boolean = true): HashMap {
        const data = super.toJSON(with_changes);
        const date = dayjs(data.date);
        data.start_epoch = date.unix();
        data.end_epoch = date.add(data.duration || 60, 'm').unix();
        data.organiser =
            this.organiser instanceof User
                ? this.organiser.toJSON()
                : {
                      email:
                          typeof this.organiser === 'string'
                              ? this.organiser
                              : this.organiser_email,
                  };
        data.auto_approve = data.space_ids.map((space) => true);
        if (data.body && !data.notes.find((note) => note.message === data.body)) {
            data.notes.push({
                type: 'description',
                message: data.body,
                author: this._service.parent.Users.current.email,
                date: dayjs().valueOf(),
            } as BookingNote);
            data.notes.sort((a, b) => b.date - a.date);
        }
        data.organiser = this._service.parent.Users.find(data.organiser_email) || {
            email: data.organiser_email,
        };
        delete data.organiser_email;
        data.attendees = data.attendee_emails.map((user) => {
            return this._service.parent.Users.find(user) || { email: user };
        });
        data.attendees = unique([data.organiser].concat(data.attendees), 'email');
        data.notify_users = [data.organiser.email];
        data.room_ids = data.space_ids;
        data.old_date = this.date;
        data.from_room = this.space ? this.space.email : '';
        delete data.attendee_emails;
        delete data.space_ids;
        delete data.date;
        delete data.duration;
        delete data.display;
        delete data.email;
        delete data.name;
        return data;
    }
}
