import { BaseDataClass } from '../base-api.class';
import { HashMap, Identity } from '../../../shared/utilities/types.utilities';
import { Booking } from '../bookings/booking.class';
import { BaseAPIService } from '../base.service';
import { BuildingLevel } from '../organisation/level.class';
import { SpaceRules } from '../bookings/booking.interfaces';
import { rulesForSpace } from '../bookings/booking.utilities';
import { User } from '../users/user.class';

import * as dayjs from 'dayjs';

export interface ISpaceAvailabilityOptions {
    /** Start date and time of the availability block */
    date?: number;
    /** Length of the availability block */
    duration: number;
    /**  */
    id?: string;
    /** List of spaces to look at the availability for */
    room_ids?: string;
    /** Whether the spaces looked at should be bookable */
    bookable?: boolean;
    /** List of spaces or zones to ignore */
    ignore?: string;
    /** List of zones to look at the availability for */
    zone_ids?: string;
    /** Whether space bookings should not be returned */
    hide_bookings?: boolean;

    clear?: boolean;
}

export interface SpaceBookingRuleOptions {
    date: number;
    duration: number;
    host: User;
}

const OPTION_DEFAULTS: ISpaceAvailabilityOptions = {
    duration: 60
};

export interface RecurrenceAvailability {
    /** Unix epoch in seconds */
    readonly date: number;
    /** Whetehr space is available at this time */
    readonly available: boolean;
}

export class Space extends BaseDataClass {
    /** Extends name for the space */
    public readonly long_name: string;
    /** CSS selector for the location of the space of the level's map */
    public readonly map_id: string;
    /** Type of space */
    public readonly type: string;
    /** Whether user can search for this space */
    public readonly can_search: boolean;
    /** Whether user can open the control URL if available */
    public readonly can_control: boolean;
    /** Hourly cost for using the space */
    public readonly hourly_rate: number;
    /** Whether space can be booked by users */
    public readonly bookable: boolean;
    /** People capacity of the space */
    public readonly capacity: number;
    /** Index to force order when sorting multiple spaces */
    public readonly sort_priority: number;
    /** Setup time for bookings in this space */
    public readonly setup: number;
    /** Breakdown time for bookings in this space */
    public readonly breakdown: number;
    /** Last returned availability result */
    public readonly was_available: boolean;
    /** URL for the control interface of the space */
    public readonly support_url: boolean;
    /** Display image for the space */
    public readonly image: string;
    /** List of recurrence occurrence availability details */
    public readonly recurr_available: readonly RecurrenceAvailability[];
    /** Engine zones associated with the space */
    public readonly zones: readonly string[];

    /** Class type of required service */
    protected __type: string = 'Space';
    /** Bookings associated with the space */
    private _bookings: string[];
    /** List of equipment available in the space */
    private _extras: Identity[];
    /** List of ids for linked spaces */
    private _linked_spaces: string[];

    constructor(raw_data: HashMap = {}) {
        super(raw_data);
        const settings = raw_data.settings || {};

        this.long_name = settings.long_name || raw_data.long_name;
        this.map_id = settings.map_id || raw_data.map_id || '';
        this.type =
            settings.book_type ||
            (this.level.settings || {}).book_type ||
            raw_data.book_type ||
            raw_data.type ||
            'book';
        this.can_search =
            settings.searchable || raw_data.searchable || raw_data.can_search || false;
        this.can_control =
            settings.controlable || raw_data.controlable || raw_data.can_control || false;
        this.hourly_rate = settings.cost_hour || raw_data.cost_hour || raw_data.hourly_rate || 0;
        this.bookable = settings.bookable || raw_data.bookable || false;
        this.setup = (settings.setup || 0) / 60 || raw_data.setup || 0;
        this.breakdown = (settings.breakdown || 0) / 60 || raw_data.breakdown || 0;
        this.capacity = raw_data.capacity || 0;
        this.recurr_available = raw_data.recurr_available || settings.recurr_available || [];
        const available = !!this.recurr_available.find(occurence => !!occurence.available);
        this.was_available =
            settings.available || raw_data.available || raw_data.was_available || available || false;
        this.support_url = raw_data.support_url;
        this.image = raw_data.image || '';

        this.zones = raw_data.zones instanceof Array ? raw_data.zones : [];
        const raw_bookings = settings.bookings || raw_data.bookings || [];
        this._bookings =
            (raw_bookings && raw_bookings.length
                ? raw_bookings.map(i => i.id)
                : raw_data._bookings) || [];
        if (this._service.parent && this._service.parent.Bookings) {
            if (raw_bookings) {
                const id_list = [];
                raw_bookings.forEach(booking => {
                    let id = booking.id;
                    if (!this._service.parent.Bookings.find(booking.id)) {
                        id = this._service.parent.Bookings.addFrom(this.id, booking, 'class');
                    }
                    id_list.push(id);
                });
                this._bookings = id_list;
            }
        }
        this._extras = [];
        this._linked_spaces =
            settings.linked_rooms || raw_data.linked_rooms || raw_data.linked_spaces || [];
    }

    /** Whether the space has a booking in progress */
    public get in_use(): boolean {
        return !!this.current;
    }

    /** Get any current in progress meeting */
    public get current(): Booking {
        const bookings = this.bookings;
        for (const bkn of bookings) {
            const status = bkn.status;
            if (status === 'in_progress' || status === 'started') {
                return bkn;
            }
        }
        return null;
    }

    /** Get the next upcoming meeting */
    public get next(): Booking {
        const now = dayjs().valueOf();
        const bookings = this.bookings;
        for (const bkn of bookings) {
            if (bkn.date > now) {
                return bkn;
            }
        }
        return null;
    }

    /** List of stored bookings for the space */
    public get bookings(): Booking[] {
        const booking_list =
            this._service && this._service.parent && this._service.parent.Bookings
                ? this._service.parent.Bookings.filter(
                      booking => this._bookings.indexOf(booking.id) >= 0
                  ) || []
                : [];
        return booking_list.sort((a, b) => a.date - b.date);
    }

    /** List of bookings for today */
    public get todays_bookings(): Booking[] {
        return this.bookingsFor(dayjs().valueOf());
    }

    /** List of ids for linked spaces */
    public get linked_spaces() {
        return [...this._linked_spaces];
    }

    /** Level in which the space is associated */
    public get level(): BuildingLevel {
        return this._service.parent.Organisation.levelWithID(this.zones as any) || new BuildingLevel({});
    }

    /**
     * Get bookings for space
     * @param date Date to filter bookings on
     */
    public bookingsFor(date?: number): Booking[] {
        if (date) {
            const day = dayjs(date);
            return this.bookings.filter(i => {
                const start = dayjs(i.date);
                return start.isSame(day, 'd');
            });
        } else {
            return this.bookings;
        }
    }

    /**
     * Whether the space is available.
     */
    public isAvailable(options?: ISpaceAvailabilityOptions): Promise<boolean> {
        options = options
            ? { ...OPTION_DEFAULTS, ...options, room_ids: this.id }
            : { ...OPTION_DEFAULTS, room_ids: this.id };
        return new Promise(resolve => {
            (this._service as any).available(options).then(
                (list: Space[]) => {
                    for (const rm of list) {
                        if (rm.id === this.id) {
                            return resolve(true);
                        }
                    }
                    resolve(false);
                },
                () => resolve(false)
            );
        });
    }

    /**
     * Make a copy of this object
     */
    public clone(): Space {
        return new Space(this);
    }

    /**
     * Make a copy of this object without identification data
     */
    public duplicate(): Space {
        return new Space({ ...this, id: null, email: null, _bookings: [] });
    }

    /**
     * Generate the booking rules for space with given options
     * @param options Conditions for generating the space rules
     */
    public rulesFor(options: SpaceBookingRuleOptions): SpaceRules {
        if (!this.level) {
            return { auto_approve: true, hide: false };
        }
        const building = this._service.parent.Organisation.buildings.find(
            bld => bld.id === this.level.building_id
        );
        if (!building) {
            return { auto_approve: true, hide: false };
        }
        const { date, duration, host } = options;
        const rules: SpaceRules = rulesForSpace({
            time: date,
            duration,
            space: this,
            user: host,
            rules: building.booking_rules
        });
        return rules;
    }

    /**
     * Whether space can only be booked by request
     * @param options Conditions for checking the space rules
     */
    public byRequest(options: SpaceBookingRuleOptions) {
        const rules = this.rulesFor(options);
        return !rules.auto_approve;
    }
}
