import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
import { first } from 'rxjs/operators';

import { ApplicationService } from 'src/app/services/app.service';
import { Building } from 'src/app/services/data/organisation/building.class';
import { ReportDetails } from '../reports.component';
import { Report } from 'src/app/services/data/reports/report.class';

import * as dayjs from 'dayjs';

@Component({
    selector: 'a-report-display',
    templateUrl: './report-display.component.html',
    styleUrls: ['./report-display.component.scss'],
})
export class ReportDisplayComponent implements OnInit, OnChanges {
    /** Details about the report being requested */
    @Input() public report: ReportDetails;
    /** Start date for the generated report */
    public start_date: number = dayjs().startOf('d').valueOf();
    /** End date for the generated report */
    public end_date: number = dayjs().endOf('d').valueOf();
    /** List of report data entries */
    public report_data: Report;
    /** Whether report data is loading */
    public loading: boolean;
    /** List of selected locations */
    public locations: Building[];

    /** List of available buildings */
    public get buildings(): Building[] {
        return this._service.Organisation.buildings;
    }

    constructor(private _service: ApplicationService) {}

    public ngOnInit(): void {
        this._service.Organisation.initialised.pipe(first(_ => _)).subscribe(() => {
            this.locations = [this._service.Organisation.building];
        })
    }

    public ngOnChanges(changes: SimpleChanges) {
        /* istanbul ignore else */
        if (changes.report) {
            this.report_data = null;
        }
    }

    /**
     * Generate report
     */
    public generateReport() {
        /* istanbul ignore else */
        if (this.report) {
            this.loading = true;
            this.report_data = null;
            this._service.Reports.show(this.report.id, {
                date: dayjs(this.start_date).format('YYYY-MM-DD'),
                start_date: Math.floor(this.start_date / 1000),
                end_date: Math.floor(this.end_date / 1000),
                zone_ids: this.locations.map((i) => i.id).join(','),
            }).then(
                (report) => {
                    this.loading = false;
                    this.report_data = new Report({ ...report, type: this.report.id });
                },
                (err) => {
                    this.loading = false;
                    this._service.notifyError(
                        `Error generating report. Error: ${err.message || err}`
                    );
                }
            );
        }
    }

    public downloadReport() {
        /* istanbul ignore else */
        if (this.report_data) {
            const date = dayjs(this.start_date).format('YYYY-MM-DD');
            this.report_data.downloadCSV(
                `${this.report.name.split(' ').join('_').toLowerCase()}+${date}.csv`
            );
        }
    }
}
