import { CurrencyPipe } from '@angular/common';

import { HashMap } from 'src/app/shared/utilities/types.utilities';
import { ReportsService } from './reports.service';
import { csvToJson, downloadFile, jsonToCsv } from 'src/app/shared/utilities/general.utilities';
import { Booking } from '../bookings/booking.class';
import { ServiceLike } from '../base-api.class';

import * as dayjs from 'dayjs';

export type BookingReport = Report<Booking>;

export class Report<T extends {} = HashMap> {
    /** Type of report */
    public readonly type: string;
    /** List of data associated with the report */
    public readonly data: T[];
    /** Map of available services for child classes */
    private static _service: ServiceLike;
    /** Set the services used to handle data model requests */
    public static setService(service: ServiceLike): void {
        Report._service = service;
    }
    /** Service for managing model on the server */
    protected get _service(): ServiceLike {
        return Report._service;
    }

    constructor(raw_data: HashMap) {
        this.type = raw_data.type || '';
        this.data = this.cleanData(raw_data.data || []);
    }

    /** Create report data structure from CSV */
    public static fromCSV(type: string, data: string): Report {
        const csv_json = csvToJson(data);
        return new Report({ type, data: csv_json });
    }

    /** Download report data as CSV format */
    public downloadCSV(name: string = 'unammed.csv'): void {
        downloadFile(name, jsonToCsv(this.data));
    }

    /** Download report data as JSON format */
    public downloadJSON(name: string = 'unnamed.json'): void {
        downloadFile(name, JSON.stringify(this.data, undefined, 4));
    }

    private cleanData(data: T[]) {
        if (data.length > 0 && this.type === 'catering') {
            const fields = Object.keys(data[0]);
            const room_field = fields.find(key => key.toLowerCase().includes('room') && key.toLowerCase().includes('email'));
            const price_field = fields.find(key => key.toLowerCase().includes('price'));
            const date_field = fields.find(key => key.toLowerCase().includes('date'));
            for (let row of data){
                /* istanbul ignore else */
                if (room_field && price_field) {
                    const room = this._service.parent.Spaces.find(row[room_field].toLowerCase());
                    const bld = this._service.parent.Organisation.buildings.find(bld => bld.id === ((room || {}).level || {}).building_id);
                    /* istanbul ignore else */
                    if (bld) {
                        row[price_field] = (new CurrencyPipe('en_us')).transform(row[price_field] / 100, bld.currency || 'USD');
                    }
                }
                /* istanbul ignore else */
                if (date_field) {
                    row[date_field] = dayjs(row[date_field]).format('DD MMM YYYY - HH:mm');
                }
                for (let field of fields) {
                    row[field] = typeof row[field] === 'string' ? row[field].replace(/\,/g, '') : row[field];
                }
            }
        } else if (this.type === 'bookings') {
           data = data.map(i => {
               const booking = new Booking(i).toJSON();
               delete booking.id;
               delete booking.icaluid;
               delete booking.type;
               delete booking.auto_approve;
               delete booking.notify_users;
               delete booking.organiser_email;
               delete booking.edits;
               delete booking.catering;
               delete booking.checked_in;
               delete booking.approval_status;
               booking.organiser = booking.organiser.name || booking.organiser.email;
               booking.equipment_codes = Object.keys(booking.equipment_codes).map(key => booking.equipment_codes[key]).join(', ');
               booking.expected_attendees = Object.keys(booking.expected_attendees).map(key => booking.expected_attendees[key]).join(', ');
               booking.attendees = booking.attendees.map(person => person.name || person.email).join(', ');

               return booking;
            }) as any;
        }
        return data;
    }
}
