import { Injectable } from '@angular/core';
import { ComposerService } from '@placeos/composer';

import { BaseAPIService } from '../base.service';
import { MapLocation } from './location.class';
import { HashMap } from 'src/app/shared/utilities/types.utilities';

@Injectable({
    providedIn: 'root'
})
export class LocationService extends BaseAPIService {
    constructor(protected _composer: ComposerService) {
        super(_composer);
        MapLocation.setService(this);
        this._name = 'Location';
        this._api_route = 'people';
    }

    public add(...args): never {
        throw new Error('Create not allowed for location service')
    }

    public update(...args): never {
        throw new Error('Update not allowed for location service')
    }

    public delete(...args): never {
        throw new Error('Delete not allowed for location service')
    }

    public process(item: HashMap) {
        return new MapLocation(item) as any;
    }
}
